import {connect} from 'react-redux'
import dateTasksComponent from '../components/tasks.js'
import {addTask, loadMore, loadUnshift} from '../actions'

const mapStateToProps = (state, ownProps) => {
    return {
        date: state.date,
        dates: state.date.dates,
        user: state.user
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return{
        addTask: (e) => {
            dispatch(addTask('ownProps.content'))
        },
        handleScroll: (e)=>{
            console.log(e.target.scrollLeft);
            // console.log('window: ',window.innerWidth);
            

        },
        handleClick: (e)=>{
            console.log(e.target);
        },
        loadMore: ()=>{
            dispatch(loadMore())
        }, 
        loadUnshift: ()=>{
            dispatch(loadUnshift())
        }
    }
}

const TasksContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(dateTasksComponent)

export default TasksContainer