import {connect} from 'react-redux'
import Bookmark from '../components/bookmark.js'
import {changeDate, addMission, toggleKeyBtn, toggleMonthBtn, getCalender, changeMonth, changeYear} from '../actions'



const getCalenderTitle = (year, month) => {
    let monthDb = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUNE', 'JULY', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
    let title = monthDb[month]
    return title
}

const mapStateToProps = (state, ownProps) => {
    return {
        bookmarkKey: state.bookmark.bookmarkKey,
        bookmarkMonth: state.bookmark.bookmarkMonth,
        date: state.date,
        calender: state.date.calender,
        calenderTitle: {
            month: getCalenderTitle(state.date.year, state.date.month),
            year: state.date.year,
        },
        tasksDateTitle: state.dates,
        tasks: state.tasks,
        user: state.user

    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        chooseDate: (year, month, date) => {
            dispatch(getCalender(year, month))
            dispatch(changeDate(year, month, date))
        },
        addMission: (content) => {
            dispatch(addMission(content))
        },
        toggleKeyBtn: ()=>{   
            dispatch(toggleKeyBtn())
        },
        toggleMonthBtn: ()=>{
            dispatch(toggleMonthBtn())
        },
        changeMonth: (month)=>{
            dispatch(changeMonth(month))
        },
        changeYear: (year)=>{
            dispatch(changeYear(year))
        }

    }
}

const BookmarkContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(Bookmark)

export default BookmarkContainer