import React from 'react';
import BulletList from './bulletList'
import host from './host'

class Task extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            status: this.props.initstatus,
            delete: false
        }
        this.handleEdit = this.handleEdit.bind(this)
        this.handleEditChange = this.handleEditChange.bind(this)
        this.handleMissionBulletType = this.handleMissionBulletType.bind(this)
        this.handleDelete = this.handleDelete.bind(this)

    }
    componentWillMount(){
        document.addEventListener('mousedown', this.handleClick)
    }
    componentWillUnmount(){
        document.removeEventListener('mousedown', this.handleClick)
    }
    handleClick = (e) => {
        if(this.node.contains(e.target)){
            return
        }  
        if(!this.state.delete){
            this.setState({status: 'view'})
        }
    }
    changeStatus = (status) => {
        this.setState({status: status})
    }
    handleEditChange(e, id){
        let updateTask = this.props.task
        updateTask.status = 'view'
        updateTask.content = e.target.value
        this.props.updateTask(id,updateTask)
        if(e.which === 13){
            this.setState({status: 'view'})
        }
    }
    handleEdit(e,id){
        e.preventDefault()
        const self = this       
        let {date, user} = this.props
        const month = this.props.date.month + 1
        if(this.props.taskType == 'tasks'){
            fetch(`${host}/tasks/${user.userId}/${date.year}/${month}/${date.date}`,{
                method: 'PUT',
                headers: { 'Content-Type': 'application/json'},
                body: JSON.stringify({
                    'content': self.props.task.content,
                    'id': id
                }),
                credentials: 'include'
            })
            let updateTask = this.props.task
            updateTask.status = 'view'
            this.setState({status: 'view'})
            this.props.updateTask(id,updateTask)
        } else {
            fetch(`${host}/${this.props.taskType}/${user.userId}/${date.year}/${month}`,{
                method: 'PUT',
                headers: { 'Content-Type': 'application/json'},
                body: JSON.stringify({
                    'content': self.props.task.content,
                    'id': id
                }),
                credentials: 'include'
            })
            let updateTask = this.props.task
            updateTask.status = 'view'
            this.setState({status: 'view'})
            this.props.updateTask(id,updateTask)
        }


    }
    handleDelete(id){
        this.changeStatus('hide')
        this.setState({delete: true})
        let {date, user} = this.props
        const month = this.props.date.month + 1
        if(this.props.taskType == 'tasks'){
            fetch(`${host}/tasks/${user.userId}/${date.year}/${month}/${date.date}`, {
                method: "DELETE",
                body: JSON.stringify({
                    'id': id
                }),
                headers: {
                    'Content-Type': "application/json"
                },
                credentials: 'include'
                })
                .then(res=>res.json())
                .then((json)=>{                            
                    if(json.deleted){
                        let updateTask = Object.assign({},this.props.task) 
                        updateTask.status = 'hide'
                        this.props.updateTask(id,updateTask)
                    }         

                })
                .catch(err => console.log(err))
        } else {
            fetch(`${host}/${this.props.taskType}/${user.userId}/${date.year}/${month}`, {
                method: "DELETE",
                body: JSON.stringify({
                    'id': id
                }),
                headers: {
                    'Content-Type': "application/json"
                },
                credentials: 'include'})
                .then(()=>{
                    let updateTask = Object.assign({},this.props.task) 
                    updateTask.status = 'hide'
                    this.props.updateTask(id,updateTask)
                })
                .catch(err => console.log(err))
        }
        
        
    }
    handleMissionBulletType(id, bulletType){
        let updateTask = this.props.task
        updateTask.bulletType = bulletType
        this.props.updateTask(id,updateTask)    
    }
    render(){
        let taskDb = this.props.task
        let task        
       

        if(this.state.status=='hide'){                        
            task = null
        }
        else if(this.state.status=='view'){
            task = <li className="listOfTask">
                        <BulletList task={taskDb} date={this.props.date} taskType={this.props.taskType} user={this.props.user} changeMissionType={this.handleMissionBulletType}/>
                        {taskDb.bulletType!=6?
                        (<span onClick={() => this.changeStatus("edit")}>{taskDb.content}</span>)
                        :(<span className="lineThrough" onClick={()=>this.handelMissionStatus(taskDb.id)}>{taskDb.content}</span>)
                        }       
                    </li>
        } 
        else {
            task = <li className="listOfTask, taskEditLi">
                        <BulletList task={taskDb} date={this.props.date} taskType={this.props.taskType} user={this.props.user} changeMissionType={this.handleMissionBulletType}/>
                        <form className="myForm" onSubmit={(e)=>this.handleEdit(e,taskDb.id)}> 
                            <input className="myInput" type="text" value={taskDb.content} onChange={(e)=>this.handleEditChange(e, taskDb.id)}></input>
                        </form>
                        <span className="delete" onClick={()=>this.handleDelete(taskDb.id)}>x</span>
                    </li>  
                 
        }
        return(
            <div ref={node=>this.node=node}>{task}</div>
            
        )

    }
}

export default Task