import React, { Component}from 'react';
import TaskList from './taskList'

class dateTasksComponent extends Component {
    constructor(props) {
        super(props)
        this.state = {
            offsetWidth: 0
        }
        this.myInput = React.createRef()
        this.handleOnScroll = this.handleOnScroll.bind(this)
        this.resizeDetection = this.resizeDetection.bind(this)

    } 
    resizeDetection(){
        let self = this        
        this.setState({
            offsetWidth: self.myInput.current.offsetWidth
        })
    }
    componentDidMount(){
        window.addEventListener('resize', this.resizeDetection);   
        let self = this        
        this.setState({
            offsetWidth: self.myInput.current.offsetWidth
        })   
    }

    handleOnScroll(e){
        let self = this;
        let scrollLeft = e.target.scrollLeft
        if(scrollLeft<0){
            self.props.loadUnshift()  
        }
        
        let offsetWidth = this.state.offsetWidth
        let realWidth = (this.props.dates.length)*270        
        if(realWidth-(scrollLeft+offsetWidth) < 100){    
            self.props.loadMore()   
        }
    }

    render(){
        let monthDb = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUNE', 'JULY', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
        let {dates, date, user} = this.props
        let tasksContainerWidth = (dates.length * 280)  
        return (
            <div className="mainArea" onScroll={this.handleOnScroll} ref={this.myInput}>
                <div className="monthTasks">
                    <div className="monthSquare">{monthDb[dates[0].month]}</div>
                        <div className="tasksContainer"  style={{width: tasksContainerWidth + 'px'}}>
                            <div style={{opacity:0, height:0+'px'}}>123</div>
                            {dates.map(dateEle=>{
                                let title = dateEle.day + ' ' + dateEle.date
                                let key = '' + dateEle.year + dateEle.month + dateEle.date                                
                                if(dateEle.date == date.date && dateEle.month == date.month && dateEle.year == date.year ){
                                    return(  
                                        <div key={key} className="dateTasksDiv chooseDay">
                                            <TaskList title={title} date={dateEle} taskType='tasks' user={user}/>
                                        </div>
                                        )
                                } else {
                                    if(dateEle.date == 1){
                                        return(    
                                            <div key={key} className="dateTasksDiv">
                                                <div className="monthSquare test">{monthDb[dateEle.month]}</div>
                                                <TaskList title={title} date={dateEle} taskType='tasks' user={user}/>
                                            </div>
                                        )
                                    } else {
                                        return(    
                                            <div key={key} className="dateTasksDiv">
                                                <TaskList title={title} date={dateEle} taskType='tasks' user={user}/>
                                            </div>
                                        )      
                                    }

                                }

                            })}  
                        </div>
                                       
                       
                </div>
            </div>
        )
    }
} 

export default dateTasksComponent