import React from "react"
import host from './host'
 
class BulletList extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            openBulletList: false
        }
        this.openBulletList = this.openBulletList.bind(this)
    }
    componentWillMount(){
        document.addEventListener('mousedown', this.handleClick)
    }
    componentWillUnmount(){
        document.removeEventListener('mousedown', this.handleClick)
    }
    handleClick = (e) => {
        if(this.node.contains(e.target)){
            return
        }
        this.setState({openBulletList: false})        
    }
    openBulletList = () => {        
        this.setState({openBulletList: true})        
    }
    handleBulletChange(updateBulletType,id){      
        const self = this        
        let {date, user} = this.props
        const month = this.props.date.month + 1
        if(this.props.taskType == 'tasks'){
            fetch(`${host}/tasks/${user.userId}/${date.year}/${month}/${date.date}/bulletType`,{
                method: "PUT",
                body: JSON.stringify({
                    'bulletType': updateBulletType,
                    'id': id
                }),
                headers: {
                    'Content-Type': "application/json"
                }})
                .then(()=>{
                    self.setState({openBulletList: false}) 
                    self.props.changeMissionType(id, updateBulletType)   
                })
        } else {
            fetch(`${host}/${this.props.taskType}/${user.userId}/${date.year}/${month}/bulletType`,{
                method: "PUT",
                body: JSON.stringify({
                    'bulletType': updateBulletType,
                    'id': id
                }),
                headers: {
                    'Content-Type': "application/json"
                }})
                .then(()=>{
                    self.setState({openBulletList: false}) 
                    self.props.changeMissionType(id, updateBulletType)   
            })
        }
        
    }
    render(){
        const selections = ['·','x',">", "<", "!", "#","-"]
        const task = this.props.task
        return(
            <span ref={node=>this.node=node}>
                <span className="bulletType" onClick={this.openBulletList}>{selections[task.bulletType]}</span>
                {this.state.openBulletList?(
                    <div className="bulletList">
                        {selections.map((selection, index)=>(
                            <div key={index} value={index} onClick={()=>this.handleBulletChange(index,task.id)}>{selection}</div>
                        ))}
                    </div>
                ):(
                    <></>
                )}     
            </span>
            
        )
    }
}

export default BulletList