import {combineReducers} from 'redux'
import { act } from 'react-dom/test-utils';
const bullet = ['.', 'x', '>', '<', 'o', '*']
let d = new Date()

const getCalender = (year,month) => {
    let days = new Date(year, month+1, 0).getDate()
    let calender = new Array(7)
    for (let i=0; i<calender.length; i++){
        calender[i] = new Array(7).fill('')
    }
    calender[0] = ['S','M','T','W','T','F','S']
    let day
    let week = 1
    day = new Date(year, month, 1).getDay()
    calender[week][day] = 1
    for (let i=2; i<=days; i++){
        day = new Date(year, month, i).getDay()
        if (day-1 < 0){
            week += 1
        }
        calender[week][day] = i
    }
    return calender
}


const getDates = (year, month, date) => {
    let dates = []
    let totalDay = new Date(year, month+1, 0).getDate()
    let weekdayName = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT']
    let day4calu = date
    let dayIndex = new Date(year, month, day4calu).getDay()
    dates[0] = {
        year: year,
        month: month,
        date: day4calu,
        day: weekdayName[dayIndex]
    }
    for(let i=1; i<5; i++){
        day4calu = day4calu + 1
        if (day4calu > totalDay){
            month = month + 1
            day4calu = 1
            if (month > 11){
                month = 0
                year = year + 1
            }
        }
        dayIndex = new Date(year, month, day4calu).getDay()
        dates[i] = {
            year: year,
            month: month,
            date: day4calu,
            day: weekdayName[dayIndex]
        }
    }    
    return dates
}

const getMoreDay = (lastDay, num) => {
    let year = lastDay.year
    let month = lastDay.month
    let date = lastDay.date
    let dates = []
    let totalDay = new Date(year, month+1, 0).getDate()
    let weekdayName = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT']
    let day4calu = date
    let dayIndex = new Date(year, month, day4calu).getDay()
    for(let i=0; i<num; i++){
        day4calu = day4calu + 1
        if (day4calu > totalDay){
            month = month + 1
            totalDay = new Date(year, month+1, 0).getDate()            
            day4calu = 1
            if (month > 11){
                month = 0
                year = year + 1
            }
        }
        dayIndex = new Date(year, month, day4calu).getDay()
        dates[i] = {
            year: year,
            month: month,
            date: day4calu,
            day: weekdayName[dayIndex]
        }
    }    
    return dates
}

const getMoreBeforeDay = (firstDay, num) => {
    let year = firstDay.year
    let month = firstDay.month
    let date = firstDay.date
    let dates = []
    let totalDay = new Date(year, month+1, 0).getDate()
    let weekdayName = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT']
    let day4calu = date
    let dayIndex = new Date(year, month, day4calu).getDay()
    for(let i=1; i<=num; i++){
        day4calu = day4calu - 1
        if (day4calu < 1){
            month = month - 1
            totalDay = new Date(year, month+1, 0).getDate()
            day4calu = totalDay
            if (month < 0){
                month = 11
                year = year - 1
            }
        }
        dayIndex = new Date(year, month, day4calu).getDay()
        dates[num-i] = {
            year: year,
            month: month,
            date: day4calu,
            day: weekdayName[dayIndex]
        }
    }    
    return dates
}


const date = (state={
    firstDay: getDates(d.getFullYear(), d.getMonth(), d.getDate())[0],
    lastDay: getDates(d.getFullYear(), d.getMonth(), d.getDate()).slice(-1)[0],
    calender: getCalender(d.getFullYear(), d.getMonth()),
    year: d.getFullYear(), 
    month: d.getMonth(), 
    date: d.getDate(), 
    day: d.getDay(),
    dates: getDates(d.getFullYear(), d.getMonth(), d.getDate())
    }, action) => {
    switch(action.type) {
        case 'CHANGE_MONTH': 
            return Object.assign({},state, {
                calender: getCalender(d.getFullYear(), action.month),
                month: action.month,
                dates: getDates(state.year, action.month, state.date),
                firstDay:  getDates(state.year, action.month, state.date)[0],
                lastDay: getDates(state.year, action.month, state.date).slice(-1)[0]
            })
        case 'CHANGE_YEAR': 
            return Object.assign({},state, {
                calender:  getCalender(action.year, d.getMonth()),
                year: action.year,
                dates: getDates(action.year, state.month, state.date),
                firstDay:  getDates(action.year, state.month, state.date)[0],
                lastDay: getDates(action.year, state.month, state.date).slice(-1)[0]
            })
        case 'CHANGE_DATE':
            return Object.assign({},state, {
                calender:  getCalender(action.year, action.month),
                year: action.year,
                month: action.month,
                date: action.date,
                day: action.day,
                dates: getDates(action.year, action.month, action.date),
                firstDay:  getDates(action.year, action.month, action.date)[0],
                lastDay: getDates(action.year, action.month, action.date).slice(-1)[0]
            })
        case 'LOAD_MORE_AFTER':
            let moreDay = getMoreDay(state.lastDay,5)
            let newDates = [...state.dates, ...moreDay]            
            return Object.assign({}, state, {
                dates: newDates,
                lastDay: moreDay.slice(-1)[0]
            })
        case 'LOAD_MORE_BEFORE':
            let moreBeforeDay = getMoreBeforeDay(state.firstDay,5)
            let newBeforeDates = [...moreBeforeDay, ...state.dates]            
            return Object.assign({}, state, {
                dates: newBeforeDates,
                firstDay: moreBeforeDay[0]
            })
        default:  
            return state
    }
}

const bookmark = (state={bookmarkKey: false, bookmarkMonth: true}, action) => {
    switch (action.type) {
        case 'TOGGLE_KEY_BTN':
            return Object.assign({}, state, {
                bookmarkKey: !state.bookmarkKey
            })
        case 'TOGGLE_MONTH_BTN':
            return Object.assign({}, state, {
                bookmarkMonth: !state.bookmarkMonth
            })
        default:
            return state
    }
}

const user = (state={userId: 0, userName: "system"}, action) =>{
    switch(action.type){
        case 'USER_LOGIN':
            return Object.assign({}, state, {
                userId: action.userId,
                userName: action.userName
            })
        case 'USER_LOGOUT':
            return Object.assign({}, state, {
                userId: 0,
                userName: "system"
            })
        default: 
            return state
    }
}


const bulletApp = combineReducers({
    bookmark,
    date,
    user
})

export default bulletApp