import React from 'react'
import host from './host'
class UserComponent extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            error: false,
            openregist: false,
            openlogin: false,
            userName: 'guest',
            userPassword: 'guest',
            openLogout: true
        }
        this.openRegister = this.openRegister.bind(this)
        this.openLogin = this.openLogin.bind(this)
        this.handleChangeInput = this.handleChangeInput.bind(this)
        this.handleLogin = this.handleLogin.bind(this)
        this.handleRegister = this.handleRegister.bind(this)
        this.closeForm = this.closeForm.bind(this)
        this.userLogout = this.userLogout.bind(this)
    }
    componentDidMount(){
        fetch(`${host}/init`, {
            credentials: "include",
        })
        .then(res=>res.json())
        .then(json=>{
            if(json.logined){                
                this.props.userLogin(json.id, json.userName)    
                this.setState({openlogin: false})
            }        
        })
    }
    closeForm(){
        this.setState({
            error: false,
            openregist: false,
            openlogin: false})    
    }
    openLogin(){
        this.setState({
            error: false,
            message: '',
            openregist: false,
            openlogin: true})
    }
    openRegister(){
        this.setState({
            error: false,
            message: '',
            openregist: true,
            openlogin: false
        })
    }
    handleChangeInput(e, inputField){
        this.setState({
            [inputField]: e.target.value
        })
    }
    handleLogin(e){
        e.preventDefault()
        if(this.state.userName == '' || this.state.userPassword == ''){
            this.setState({
                error: true,
                message: '請輸入帳號及密碼'
            })
        } else {
            fetch(`${host}/userLogin`,{
                method: 'POST',
                body: JSON.stringify({
                    userName: this.state.userName,
                    password: this.state.userPassword
                }),
                headers: {
                    'Content-Type': "application/json"
                },
                credentials: "include",
            }).then(res=>res.json())
            .then(json=>{
                if(json.success){
                    this.props.userLogin(json.id, json.userName)    
                    this.setState({openlogin: false})
                } else {
                    this.setState({
                        error: true,
                        message: json.message,
                        userName: '',
                        userPassword: ''
                    })
                }
            })  
        }
        
    }
    handleRegister(e){ 
        e.preventDefault()        
        if(this.state.userName == '' || this.state.userPassword == ''){
            this.setState({
                error: true,
                message: '請輸入帳號及密碼'
            })
        } else {            
            fetch(`${host}/userRegister`,{
                method: 'POST',
                body: JSON.stringify({
                    userName: this.state.userName,
                    password: this.state.userPassword
                }),
                headers: {
                    'Content-Type': "application/json"
                },
                credentials: "include",
            })
            .then(res=>res.json())
            .then(json=>{
                if(json.success){
                    this.setState({
                        openregist: false,
                    })
                    this.props.userLogin(json.id, json.userName)
                } else {
                    this.setState({
                        error: true,
                        message: json.message,
                        userName: '',
                        userPassword: ''
                    })
                }
            })
        }
    }
    userLogout(){
        let self = this
        fetch(`${host}/userLogout`,{
            credentials: "include",
        })
        .then((res)=>{self.props.userLogout()})
    }
    render(){
        return(
                <>
                {this.props.user.userId==0
                ?<div className="loginbtn unloginBtn" onClick={this.openLogin}>登入/註冊</div>
                :<>
                <div className="loginbtn loginUser">
                <span>{this.props.user.userName}</span>'s Bullet Journal / <span className="logoutbtn" onClick={this.userLogout}>登出</span>
                </div>
                </>
                
                }

                
                
                {this.state.openlogin?
                <div className="fixedBg">
                <div className="loginForm">
                    <div className="formTitle">Open Your Bullet!</div>
                    {this.state.error?<span className="errormessage">{this.state.message}</span>:<></>}
                    <span className="close" onClick={this.closeForm}>x</span>
                    <form onSubmit={this.handleLogin}>
                        <input className="myInput" type="text" placeholder="帳號" value={this.state.userName} onChange={(e)=>this.handleChangeInput(e, 'userName')}></input>
                        <input className="myInput" type="password" placeholder="密碼" value={this.state.userPassword} onChange={(e)=>this.handleChangeInput(e, 'userPassword')}></input>
                        <input className="myInput" type="submit" value="登入"/>
                    </form>
                    <div>還沒註冊嗎？<span onClick={this.openRegister}>註冊</span></div>
                    <div className="warningmessage">此為練習作品，請勿輸入真實帳密</div>
                    
                </div>
                </div>:
                <>{null}</>
                }

                {this.state.openregist?
                <div className="fixedBg">
                <div className="loginForm">
                    <div className="formTitle">Start Your Bullet!</div>
                    <span className="close" onClick={this.closeForm}>x</span>
                    {this.state.error?<span className="errormessage">{this.state.message}</span>:<></>}
                    <form onSubmit={this.handleRegister}>
                        <input className="myInput" type="text" placeholder="帳號" value={this.state.userName} onChange={(e)=>this.handleChangeInput(e, 'userName')}></input>
                        <input className="myInput" type="password" placeholder="密碼" value={this.state.userPassword} onChange={(e)=>this.handleChangeInput(e, 'userPassword')}></input>
                        <input className="myInput" type="submit" value="註冊"/>
                    </form>
                    <div>已經有帳號了!<span onClick={this.openLogin}>登入</span></div>
                    <div><span className="warningmessage">此為練習作品，請勿輸入真實帳密</span></div>
                </div>
                </div>:
                <>{null}</>
                }
                </>
            
        )
    }
}

export default UserComponent