import React from 'react';
import Task from './task'
import host from './host'

class AddTask extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            add: false,
            newMission: '',
        }
        this.handleAdd = this.handleAdd.bind(this)
        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    
    componentWillMount(){
        document.addEventListener('mousedown', this.handleClick)
    }
    componentWillUnmount(){
        document.removeEventListener('mousedown', this.handleClick)
    }
    handleClick = (e) => {
        if(this.node.contains(e.target)){
            return
        }  
        this.setState({add: false})
    }
    handleSubmit(e){
        const self = this
        e.preventDefault()
        let {date, user} = this.props
        let month = date.month +1
        if(this.props.taskType == 'tasks'){
            fetch(`${host}/tasks/${user.userId}/${date.year}/${month}/${date.date}`,{
                method: 'POST',
                headers: { 'Content-Type': 'application/json'},
                body: JSON.stringify({
                    'content': self.state.newMission,
                    'bulletType': 0
                })
            })
            .then((res) => (res.json()))
            .then((json)=>{
                let newTask = {
                    ...json,
                    status: 'view',
                    bulletList: false
                }
                this.setState({
                    add: false,
                    newMission: ''
                })  
                this.props.addNewTask(json.id, newTask)                        
             })
            .catch(err => console.log(err))
        } else {
            fetch(`${host}/${this.props.taskType}/${user.userId}/${date.year}/${month}`,{
                method: 'POST',
                headers: { 'Content-Type': 'application/json'},
                body: JSON.stringify({
                    'content': self.state.newMission,
                    'bulletType': 0
                })
            })
            .then((res) => (res.json()))
            .then((json)=>{
                let newTask = {
                    ...json,
                    status: 'view',
                    bulletList: false
                }
                this.setState({
                    add: false,
                    newMission: ''
                })  
                this.props.addNewTask(json.id, newTask)                        
                })
            .catch(err => console.log(err))
        }
    }
    handleChange(e){
        this.setState({newMission: e.target.value})
    }
    handleAdd(){
        this.setState({add: true})
    }
    
    render(){
        const selections = ['·','x',">", "<", "!", "#","-"]        
        return( <div ref={node=>this.node=node}>
                    {this.state.add?(
                        <li key={this.props.date.date}  className="taskEditLi">
                            <div className="flex">
                                <span className="bulletArea bulletArea_add">
                                    <span className="bulletType">{selections[0]}</span>
                                </span>
                                <form className="myForm" onSubmit={this.handleSubmit}> 
                                    <input className="myInput" type="text" value={this.state.newMission} onChange={this.handleChange}></input>
                                </form>
                            </div>
                        </li>
                        ):(
                        <li key={this.props.date.date} ><div onClick={this.handleAdd}>+</div></li>
                        )
                    }
                </div>
        )
    }
}
class TaskList extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            add: false,
            newMission: '',
        }
        this.handleUpdateTask = this.handleUpdateTask.bind(this)
        this.handleAddTask = this.handleAddTask.bind(this)
    }
    componentDidMount(){
        let {date, user} = this.props    
        this.node = React.createRef()
        const self = this    
        let month = date.month + 1
        const updateTask = {}
        if(this.props.taskType == 'tasks'){            
            fetch(`${host}/tasks/${user.userId}/${date.year}/${month}/${date.date}`,{
                credentials: 'include'
            })
            .then(res=>(res.json()))
            .then(json=>{
                for (let i = 0; i < json.length; i++) {
                    updateTask[json[i].id] = json[i]
                    updateTask[json[i].id].bulletList = false
                    updateTask[json[i].id].status = 'view'
                }  
                self.setState({
                    tasks:updateTask,
                })       
            })
        } else {            
            fetch(`${host}/${this.props.taskType}/${user.userId}/${date.year}/${month}`,{
                credentials: 'include'
            })
            .then(res=>(res.json()))
            .then(json=>{                
                for (let i = 0; i < json.length; i++) {
                    updateTask[json[i].id] = json[i]
                    updateTask[json[i].id].bulletList = false
                    updateTask[json[i].id].status = 'view'
                }  
                self.setState({
                    tasks:updateTask,
                })       
            })  
        }
    
    }
    componentDidUpdate(prevProps, prevState) {
        if(prevProps.date.month != this.props.date.month || prevProps.date.year != this.props.date.year ||  prevProps.user.userId != this.props.user.userId){
            let {date, user} = this.props    
            this.node = React.createRef()
            const self = this    
            let month = date.month + 1
            const updateTask = {}
            if(this.props.taskType == 'tasks'){            
                fetch(`${host}/tasks/${user.userId}/${date.year}/${month}/${date.date}`,{
                    credentials: "include"
                })
                .then(res=>(res.json()))
                .then(json=>{
                    for (let i = 0; i < json.length; i++) {
                        updateTask[json[i].id] = json[i]
                        updateTask[json[i].id].bulletList = false
                        updateTask[json[i].id].status = 'view'
                    }  
                    self.setState({
                        tasks:updateTask,
                    })       
                })
            } else {
                fetch(`${host}/${this.props.taskType}/${user.userId}/${date.year}/${month}/`,{
                    credentials: 'include'

                })
                .then(res=>(res.json()))
                .then(json=>{
                    for (let i = 0; i < json.length; i++) {
                        updateTask[json[i].id] = json[i]
                        updateTask[json[i].id].bulletList = false
                        updateTask[json[i].id].status = 'view'
                    }  
                    self.setState({
                        tasks:updateTask,
                    })       
                })
            }
        }
    }
    
    handleAddTask = (id, updateTask) => {
        let updateTasks = Object.assign({},this.state.tasks)    
        updateTasks[id] = updateTask  
        this.setState({tasks:updateTasks})
    }
    handleUpdateTask = (id,updateTask) =>{
        let updateTasks = Object.assign({},this.state.tasks)    
        updateTasks[id] = updateTask  
        this.setState({tasks:updateTasks})
    }
    render(){
        let tasks = []
        if (this.state.tasks){
            for(let key in this.state.tasks){                
                let task = this.state.tasks[key]
                tasks.push(<Task key={key} initstatus={task.status} taskType={this.props.taskType} task={task} date={this.props.date} user={this.props.user} updateTask={this.handleUpdateTask}/>)
            }
        } else {
            tasks = null
        }
        return(
        <div className="tasksStyle">
            <div className="title">{this.props.title}</div>
            <ul className="tasksList">
                {tasks}
                <AddTask taskType={this.props.taskType} date={this.props.date} addNewTask={this.handleAddTask} user={this.props.user}/>      
            </ul>
        </div> 
        )
    } 
}

export default TaskList