import React from 'react';
import TaskList from './taskList'
import plannerLogo from '../logos/planner.png'
import bulletLogo from '../logos/bullet.png'
const KeyBookmarkBtn = (props) => (
    <div className="bookmarkBtn bookmarkBtnKey" onClick={props.onClick}>
        <img src={plannerLogo} alt='planner_logo'></img>
    </div>
)

const MonthBookmarkBtn = (props) => (
    <div className="bookmarkBtn bookmarkBtnMonth" onClick={props.onClick}>
        <img src={bulletLogo}  alt='bullet_logo'></img>
    </div>
)

class MonthList extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            openList: false,
        }
        this.selectMonth = this.selectMonth.bind(this)
    }
    componentWillMount(){
        document.addEventListener('mousedown', this.handleClick)
    }
    componentWillUnmount(){
        document.removeEventListener('mousedown', this.handleClick)
    }
    handleClick = (e) => {
        if(this.node.contains(e.target)){
            return
        }  
        this.setState({openList: false})
    }
    selectMonth(index){
        this.props.changeMonth(index)
        this.setState({openList: false})      
    }
    render(){
        let monthDb = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUNE', 'JULY', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
        return(
            <span className="relative" ref={node=>this.node=node}>
            <div className="monthItem" onClick={()=>this.setState({openList:true})}>{this.props.title}</div>
            <div className="monthList">
                {this.state.openList? 
                    monthDb.map((month,index) => <div key={index} className="monthItem" onClick={()=>this.selectMonth(index)}>{month}</div>):<></>}
            </div>
         </span>
        )
    }
}
class YearList extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            openList: false,
        }
        this.selectYear = this.selectYear.bind(this)
    }
    componentWillMount(){
        document.addEventListener('mousedown', this.handleClick)
    }
    componentWillUnmount(){
        document.removeEventListener('mousedown', this.handleClick)
    }
    handleClick = (e) => {
        if(this.node.contains(e.target)){
            return
        }  
        this.setState({openList: false})
    }
    selectYear(year){
        this.props.changeYear(year)
        this.setState({openList: false})      
    }
    render(){
        let yearDb = []
        for(let i = 0; i <20; i++){
            yearDb[i] = 2020+i
        }
        return(
            <span className="relative" ref={node=>this.node=node}>
            <div className="monthItem" onClick={()=>this.setState({openList:true})}>{this.props.title}</div>
            <div className="monthList">
                {this.state.openList? 
                    yearDb.map((year,index) => <div key={index} className="monthItem" onClick={()=>this.selectYear(year)}>{year}</div>):<></>}
            </div>
         </span>
        )
    }
}
class Calender extends React.Component{
    render(){
        let calender = this.props.calender.map((week, index_w)=>(
        <div key={index_w}>
            {week.map((day,index_d)=>{
                let date = '' + this.props.date.year + this.props.date.month + day
                let key = '' + index_w + index_d            
                let presentDate = '' + this.props.date.year + this.props.date.month + this.props.date.date
                if (date == presentDate){
                    return(<div key={key} className='chooseDate'>
                        {day}
                    </div>)  
                }
                else{
                    if(Number.isInteger(day)){
                        return(
                        <div key={key} className="dateInCalender" onClick={()=>this.props.onClick(this.props.date.year, this.props.date.month, day)} >
                            {day}
                        </div>) 
                    }else{
                        return(
                        <div key={key} >
                            {day}
                        </div>)                         
                    }
                }
                })}
        </div>))

        return (
        <div className="tasksStyle">
            <div className="calenderTitle">
                <MonthList title={this.props.calenderTitle.month} changeMonth={this.props.changeMonth}/>
                <YearList title={this.props.calenderTitle.year} changeYear={this.props.changeYear}/>
            </div>
            <div className="calender">
                {calender}
            </div>
        </div>
    )}
}

const Bookmark = (props) => {
    let keyBookmark = <div className="keyBookmark">
        <ul>
            <li>
                <span>.</span>
                <span>Task</span>
            </li>
            <li>
                <span>x</span>
                <span>Finished</span>
            </li>
            <li>
                <span>&gt;</span>
                <span>Migrated</span>
            </li>
            <li>
                <span>&lt;</span>
                <span>Planned</span>
            </li>
            <li>
                <span>!</span>
                <span>Idea</span>
            </li>
            <li>
                <span>#</span>
                <span>Event</span>
            </li>
            <li>
                <span>-</span>
                <span>Delete</span>
            </li>
        </ul>
    </div>
    let monthBookmark  = <div className="monthBookmark">
                            <Calender calender={props.calender} changeYear={props.changeYear} changeMonth={props.changeMonth} calenderTitle={props.calenderTitle} date={props.date} year="2019" onClick={(year, month, date) => props.chooseDate(year, month, date)}/>
                            <TaskList title="MISSIONS" date={props.date} taskType='missions' user={props.user}/>
                            <TaskList title="NOTES" date={props.date} taskType='notes' user={props.user}/>
                        </div>
    let monthBookmarkEmpty = <div className="monthBookmarkEmpty"></div>
    return (
        <div className="bookmark">
            <KeyBookmarkBtn onClick={props.toggleKeyBtn}/>
            <MonthBookmarkBtn onClick={props.toggleMonthBtn}/>
            {props.bookmarkKey? keyBookmark : null}
            {props.bookmarkMonth? monthBookmark: monthBookmarkEmpty}
        </div>
    )
}

export default Bookmark